.login-main{
width: 100%;
  
 padding: 3rem;
}
.auth-section img{
  height: 35rem;
 
}
span{
  color: #007ac6;
  text-decoration: underline;
}
.auth-section{

  margin-top: 1rem;
  display: flex;
  justify-content: center;
  padding: 1rem 1rem 1rem 1rem ;
  align-items: center;
  
}
.auth-container-2{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.auth-container-2 form{
  width: 100%;
  padding: 20px;
  
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  box-shadow: 0 10px 25px rgb(0 0 0 / 5%),
  0 20px 48px rgb(0 0 0 / 5%),
  0 1px 4px rgb(0 0 0 / 10%);
  
}

.auth-container-2 form label input{
  padding: 7px;
  width: 100%;
  border: solid 1px #0000003e;
  border-radius: 4px;
  font-size: 13px;
}
input:focus{
  outline: 1px solid #007ac6;
}

.auth-container-2 form label:nth-child(1) h4,
.auth-container-2 form label:nth-child(2) h4,
.auth-container-2 form label:nth-child(3) h4{
  margin-bottom: 5px;
  margin-top: 10px;
}

.auth-container-2 form label:nth-child(4){
  display: flex;
}

.auth-container-2 form label:nth-child(4) input{
  width: 15%;
  margin: 13px 0px;
}
.login-logo{
  margin-bottom: 2rem;
}

.auth-btn{
  margin-top: 25px;
  padding: 10px 5px;
  background-color: #009dff;
  border: solid 1px #009dff;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s;
  font-size: 13px;
  font-weight: 500;
  width: 70%;
  
}

.auth-btn:hover{
  background-color: #018ce3;
}

.handle-switch-btn{
  background-color: transparent;
  color: #007ac6;
  border: none;
  font-size: 13px;
  cursor: pointer;
}
@media (max-width: 1000px){

 .auth-section img{
  height: 25rem;
 
}
   }
   @media (max-width: 820px) {
    .auth-section{
      flex-direction: column;
      margin-top: 1rem;
      margin-bottom: 3rem;
     
     }
    
     .auth-section img{
      height: 20rem;
     
    }
    .login-main{
      padding: 0rem;
    }
   }